// Export animation to reuse it in other components
export const rise = keyframes({
  from: { transform: "translateY(10%)", opacity: 0 },
  to: { transform: "translateY(0)", opacity: 1 },
});

const useStyles = createStyles((theme, _params, getRef) => ({
  wholeGrid: {
    width: "60%",
    // marginTop: "10%",
    marginBottom: 0,
    marginLeft: "auto",
    marginRight: "auto",
    animation: `${rise} 1s ease-out 1`,
  },

  GridItem: {
    backgroundColor: "rgba(255, 255, 255, 0.7)",
    borderRadius: "5%",
    boxSizing: "border-box",
    border: "1px solid rgba(29, 53, 87, 0.25)",
    boxShadow: "0px 0px 4px rgba(29, 53, 87, 0.2)",
    transition: "300ms",
    color: "rgba(29, 53, 87, 1)",
    height: "100%",
    "&:hover": {
      backgroundColor: "rgba(255, 255, 255, 1)",
      transform: "translateY(-3.5%) scale(1.01)",
      zIndex: 100,
      boxShadow: "8px 8px 8px rgba(29, 53, 57, 0.4)",
    },
  },

  ButtonItem: {
    marginTop: "10%",
    backgroundColor: "rgba(255, 255, 255, 0.0)",
    opacity: "0.8",
    transition: "200ms",
    color: "rgba(29, 53, 87, 1)",
    border: "1px solid rgba(29, 53, 87, 0.9)",
    animation: `${rise} 1s ease-out 1`,
    marginBottom: "25%",
    "&:hover": {
      backgroundColor: "rgba(29, 53, 87, 1)",
      color: "rgba(255, 255, 255, 1)",
      opacity: 1,
      zIndex: 100,
    },
  },
}));

function ModalNotebook(image, notebook, title, t1, t2, t3, t4, text) {
  const [opened, setOpened] = useState(false);
  const theme = useMantineTheme();
  const { classes } = useStyles();
  const secondaryColor =
    theme.colorScheme === "dark" ? theme.colors.dark[1] : theme.colors.gray[7];
  const DARK_MODE = true;

  return (
    <>
      <Modal
        opened={opened}
        onClose={() => setOpened(false)}
        size="70%"
        styles={{
          root: { maxWidth: "1500px", margin: "auto" },
        }}
      >
        <JupyterNotebookViewer
          filePath={notebook}
          className="notebook-class"
          notebookInputLanguage="python"
          // notebookOutputLanguage="python"
          inputCodeDarkTheme={DARK_MODE}
          outputDarkTheme={DARK_MODE}
          inputMarkdownDarkTheme={DARK_MODE}
          showInputLineNumbers={true}
          showOutputLineNumbers={false}
          outputTextClassName="output-text"
          inputTextClassName="input-text"
          outputBlockClassName="output-block"
          outputImageClassName="output-image"
          outputOuterClassName="output-outer"
          inputOuterClassName="input-outer"
          outputBorderClassName="output-border"
          inputBorderClassName="input-border"
          outputTableClassName="output-table"
          inputMarkdownBlockClassName="input-markdown-block"
          inputCodeBlockClassName="input-code-block"
          withOnClick={true}
          hideCodeBlocks={false}
          hideMarkdownBlocks={false}
          hideAllOutputs={false}
          hideAllInputs={false}
          remarkPlugins={[remarkMath]}
          rehypePlugins={[rehypeKatex]}
        />
      </Modal>

      <Card
        shadow="sm"
        p="lg"
        className={classes.GridItem}
        component="a"
        href="#"
        onClick={() => setOpened(true)}
      >
        <Card.Section>
          <Image src={image} height={160} />
        </Card.Section>

        <Group
          // position="apart"
          spacing="xs"
          style={{ marginBottom: 5, marginTop: theme.spacing.sm }}
        >
          <Text weight={500}>{title}</Text>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t1}
          </Badge>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t2}
          </Badge>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t3}
          </Badge>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t4}
          </Badge>
        </Group>

        <Text size="sm" style={{ color: secondaryColor, lineHeight: 1.5 }}>
          {text}
        </Text>
      </Card>
    </>
  );
}

