function AlertSafari() {
  if (isSafari)
    return (
      <Alert icon={<AlertCircle size={16} />} title="Bummer!" color="red">
        Due to low compatibility with P5.js (in the background), Safari may not
        be well supported on this website. Consider switching to any other
        browser if encountering issues.
      </Alert>
    );
}

